import React, {useEffect, useState} from 'react'
import axios from 'axios'
import { useDispatch, useSelector } from 'react-redux'
import {activeEmp} from '../actions/employeeActions'
import EmployeeProfileImage from './EmployeeProfileImage'
import EmployeeInfoScreen from './EmployeeInfoScreen'
import { Container, Row, Col } from 'react-bootstrap'


const HomeScreen = () => {
    const [companyName, setCompanyName] = useState('')
    const [userName, setUserName] = useState('')
    const [userID, setUserID] = useState('')

    const userLogin = useSelector(state => state.userLogin)
    const {userInfo} = userLogin
    console.log(userInfo[0].ECODE)
    
    const dispatch = useDispatch()

    useEffect(() => {
        getCompanyName()
        dispatch(activeEmp())
        
        if (userInfo) {
            const uname = userInfo[0].UNAME;
            setUserID(userInfo[0].ECODE);
            setUserName(uname)
        }
    }, [userInfo])

      const  getCompanyName = async () => {
      const res = await axios.get('http://localhost:3000/api/companyprofile') 
      var data = res.data
      setCompanyName(data[0].TITLE)       
    }
    
    return (
        <div>
            <h3>Welcome to {companyName}</h3> 
            <br />
          <Container>
            <Row>
              <Col><h5 className='p-2'>Hello {userName} hope you are having a good day. </h5> 
               <div style={{border: '1px solid gray', padding: '10px', borderRadius: '8px'}}>
                <EmployeeInfoScreen />  
               </div>
              </Col> 
              <Col> <EmployeeProfileImage userID={userID}/> </Col>            
            </Row>
            <Row>
             
            </Row>
          </Container>  
        </div>

    )
}

export default HomeScreen
