import {  Container } from 'react-bootstrap'
import Header from './components/Header';
import Footer from './components/Footer';
//import SideNav from './components/SideNav'
import HomeScreen from './screens/HomeScreen'
import { BrowserRouter as Router, Route } from 'react-router-dom'
import EmployeeInfoScreen from './screens/EmployeeInfoScreen'
import EmployeeTiming from './screens/EmployeeTiming'
import {StationaryOrder} from '../src/actions/stationaryActions'
import LoginScreen from './screens/LoginScreen';
import MeetingScreen from './screens/MeetingScreen';
import EmployeeDashboard from './screens/EmployeeDashboard';
import EmployeeApplication from './screens/EmployeeApplication';
import EmployeeProfileImage from './screens/EmployeeProfileImage';
import EmployeeSalary from './screens/EmployeeSalary';

function App() {
 return (
   <>
    <Router>
      <Header />
      <main className= 'py-3'>
          <Container> 
            <Route path='/login' component={LoginScreen} exact />
            <Route path='/stationeryOrder' component ={StationaryOrder} exact/>
            <Route path='/EmployeeDashboard' component ={EmployeeDashboard} exact/>
            <Route path='/EmployeeProfile' component ={EmployeeInfoScreen} exact/>
            <Route path='/EmployeeApplication' component ={EmployeeApplication} exact/>
            <Route path='/employeeImage' component={EmployeeProfileImage} exact />
            <Route path='/EmployeeTiming' component={EmployeeTiming} exact />
            <Route path='/EmployeeSalary' component={EmployeeSalary} exact />
            <Route path='/BookAppointment' component={MeetingScreen} exact />
            <Route path='/' component ={HomeScreen} exact/>
          </Container>
      </main>
      <Footer />
    </Router>  
   </>
 )
}

export default App;